.Ventana {
  background-color: white;
  z-index: 500;
  position: fixed;
  width: 400px;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px black;
  padding: 2em;
  left: 31%;
  top: 30%;
  box-sizing: border-box;
  transition: all 0.3s ease-out;
}

@media (min-width: 600px) {
  .Modal {
    width: 500px;
    left: calc(50% - 250px);
  }
}
